.gameCanvas {
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
  margin: auto;
  background-color: aquamarine;
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #d5d5d5;
}

@media only screen and (max-width: 1024px) {
  .loginWrapper .wrapper {
    height: 270px;
  }

  .loginMotherWrapper {
    height: 190px;
  }

  .lobbyWrapper .slick-slider {
    top: 105px;
  }

  .lobbyWrapper .slick-slider .wrapper .imgWrapper {
    width: 580px;
    height: 410px;
  }

  .lobbyWrapper .slick-slider .slick-next {
    right: 110px;
  }

  .lobbyWrapper .slick-slider .slick-prev {
    left: 110px;
  }

  .joker12Card .chipBtn {
    width: 54px;
    height: 54px;
  }

  .joker12Card .chipWrapper {
    margin-right: 0;
  }
  .joker12Card .cardHolder .cardItem {
    margin: 0px;
    width: 25%;
    margin-bottom: 13px;
    height: 110px;
  }
  .joker12Card .utilButton button {
    width: 95px;
  }
  .joker12Card .timerWrapper {
    width: 100px;
    height: 100px;
  }
  .randomPick button {
    width: 40px;
  }
  .joker12Card .currentTime label {
    font-size: 16px;
  }
  .resultPanelWrapper .resultPane.Last .resultImages img {
    width: 40px;
  }
  .joker12Card .cardHolder .cardItem.groupCard {
    width: 25%;
    height: 90px;
  }
  .joker12Card .cardHolder .cardItem.groupCard::after {
    font-size: 14px;
  }
  .joker12Card .cardHolder .cardItem.infoBox .content label {
    font-size: 12px;
  }
  .joker12Card .wheelWrapper {
    height: 335px;
  }
  .joker12Card .wheelWrapper .marker {
    width: 60px;
  }
  .joker12Card .wheelWrapper .currentResult {
    width: 70px;
  }
  .joker12Card .wheelWrapper .currentResult .multiplier label {
    font-size: 40px;
  }
}

@media only screen and (max-width: 1280px) and (min-width: 1100px) {
  .lobbyWrapper .slick-slider {
    top: 70px;
  }

  .lobbyWrapper .slick-slider .wrapper .imgWrapper {
    width: 655px;
    height: 465px;
  }

  .lobbyWrapper .slick-slider .wrapper h2 {
    margin-top: 80px;
  }
}

.disableScrollbar::-webkit-scrollbar {
  display: none;
}

.disableScrollbar::-webkit-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
