.spin2win.gameCanvas {
  background-image: url("../../images/spin2win/bg.png");
  background-position: center;
  background-size: cover;
}

.spin2win .gameHeader {
  width: 100%;
  height: 70px;
  position: relative;
  display: flex;
  justify-content: end;
  padding-top: 5px;
}

.elementBgImg {
  width: 100%;
  height: 100%;
  object-fit: fill;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.spin2win .gameHeader .resultPane {
  height: 50px;
  background-image: url("../../images/spin2win/resultpane.png");
  background-position: center;
  background-size: cover;
  position: relative;
  border-radius: 6px;
  overflow: scroll;
  padding: 5px;
  display: flex;
  align-items: center;
}

.spin2win .gameHeader .resultPane ul {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  z-index: 1;
  list-style: none;
}

.spin2win .gameHeader .resultPane ul li {
  width: 40px;
  height: 40px;
  background-color: #ff0000;
  border-radius: 5px;
  text-align: center;
  color: #fff;
  margin-right: 4px;
}

.spin2win .gameHeader .resultPane ul li:nth-child(n) {
  background-color: #f30d70;
}

.spin2win .gameHeader .resultPane ul li:nth-child(2n) {
  background-color: #724ea7;
}

.spin2win .gameHeader .resultPane ul li:nth-child(3n) {
  background-color: #e36e04;
}

.spin2win .gameHeader .resultPane ul li:nth-child(4n) {
  background-color: #0466c3;
}

.spin2win .gameHeader .resultPane ul li:nth-child(5n) {
  background-color: #32b73a;
}

.spin2win .gameHeader .resultPane ul li:nth-child(6n) {
  background-color: #2f0662;
}

.spin2win .gameHeader .resultPane ul li:nth-child(7n) {
  background-color: #0f8c9c;
}

.spin2win .gameHeader .resultPane ul li span {
  display: block;
}

.spin2win .gameHeader .resultPane ul li span.result {
  font-size: 26px;
  line-height: 25px;
}

.spin2win .gameHeader .resultPane ul li span.gameId {
  font-size: 13px;
  font-weight: 900;
  line-height: 15px;
  font-family: "Roboto Condensed";
}

.spin2win .gameHeader .pointsHolder,
.spin2win .gameHeader .accHolder {
  padding: 0 10px;
  height: 50px;
  /* background-image: url('../../images/spin2win/resultpane.png');
    background-position: center;
    background-size: cover; */
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}

.spin2win .gameHeader .pointsHolder label,
.spin2win .gameHeader .accHolder label {
  color: #fff;
  font-weight: 400;
  position: relative;
  z-index: 1;
  font-size: 24px;
}

.spin2win .gameHeader .pointsHolder label.point {
  font-family: Bungee Tint;
  font-size: 30px;
}

.spin2win .gameHeader .controlBtns button {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  position: relative;
  border: none;
  background-color: transparent;
}

.spin2win .gameHeader .accHolder {
  width: 100%;
  text-align: center;
  justify-content: center;
  border-radius: 6px;
}

.spin2win .gameHeader .accHolder label {
  width: 100%;
  text-transform: uppercase;
}

.spin2win .gameHeader .simpleOptions {
  position: relative;
  z-index: 1;
  height: 50px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: max-content;
  padding: 0 5px;
}

.spin2win .gameHeader .simpleOptions label {
  color: #fff;
}

/* -------------------------------------------- */

.spin2win .gameBody {
  margin-top: 20px;
}

.spin2win .gameBody .wheelWrapper .resultWheel {
  width: 500px;
  height: 500px;
  position: relative;
  /* transition: 5s ease-in-out; */
}

.spin2win .gameBody .wheelWrapper .resultWheel img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
}

.spin2win .gameBody .wheelWrapper .resultWheel img.wheelRing {
  z-index: 2;
  width: 110%;
  height: 110%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.spin2win .gameBody .wheelWrapper .resultWheel img.wheelMulti {
  z-index: 3;
  width: 50%;
  height: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.spin2win .gameBody .wheelWrapper .wheelBase {
  width: 100%;
  height: 200px;
  position: absolute;
  bottom: -150px;
  z-index: 1;
}

.spin2win .gameBody .wheelWrapper .wheelBase .wheelLabels {
  width: 350px;
  display: flex;
  justify-content: space-between;

  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 3;
}

.spin2win .gameBody .wheelWrapper .wheelBase .wheelLabels label {
  font-size: 24px;
  text-align: center;
  color: #fff;
}

.spin2win .gameBody .wheelWrapper .resultWheel .wheelMultiplier {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;

  text-align: center;
}

.spin2win .gameBody .wheelWrapper .resultWheel .wheelMultiplier label {
  font-size: 120px;
  font-family: "Bungee Inline";
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 50px;

  background: #cf0000;
  background: linear-gradient(to top, #cf0000 0%, #ffa500 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 2px rgb(255, 255, 255);
  text-stroke: 2px rgb(255, 255, 255);
  text-shadow: rgba(62, 66, 66, 0.4) 0px 10px 24px;
}

/* ---------------------------------------------- */

.spin2win .gameBody .betWrapper {
  margin-top: 20px;
}

.spin2win .gameBody .betWrapper .col-2 {
  width: 20%;
  margin-bottom: 20px;
}

.spin2win .gameBody .betBtn {
  width: 110px;
  height: 110px;
  position: relative;
  z-index: 3;
  border: 0;
  background-color: transparent;
  box-shadow: var(--base-shadow1);

  border-radius: 10px;
  overflow: hidden;
}

.spin2win .gameBody .betBtn label.amount {
  position: relative;
  z-index: 2;
  color: #000;
  font-size: 30px;
  top: 25px;
}

/* -------------------------------------- */
.spin2win .gameBody .chipWrapper {
  margin-top: 5px;
}

.spin2win .gameBody .chipBtn {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: none;
  background-color: transparent;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: 0.1s ease-in-out;
  z-index: 2;
}

.spin2win .gameBody .chipBtn img {
  z-index: 2;
}

.spin2win .gameBody .chipBtn.selected {
  transform: scale(1.1);
}

.spin2win .gameBody .chipBtn.selected label {
  font-family: Bungee Tint;
}

.spin2win .gameBody .chipBtn.selected img {
  -webkit-animation: chipRotate 4s infinite linear;
}

.spin2win .gameBody .chipBtn.selected::after {
  content: "";
  width: 90px;
  height: 90px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* background-color: yellowgreen; */
  border-radius: 50%;
  z-index: 1;
  -webkit-box-shadow: 0px 0px 45px 15px rgba(45, 255, 196, 0.9);
  -moz-box-shadow: 0px 0px 45px 15px rgba(45, 255, 196, 0.9);
  box-shadow: 0px 0px 45px 15px rgba(45, 255, 196, 0.9);
}

.spin2win .gameBody .chipBtn label.amount {
  position: relative;
  z-index: 3;
  color: #000;
  font-size: 22px;
}

/* -------------------------------------- */

.spin2win .gameFooter {
  position: relative;
  bottom: 170px;
  left: 0;
  width: 100%;
  height: 350px;
  z-index: 0;
}

.spin2win .gameFooter .timerWrapper {
  width: 250px;
  height: 150px;
  position: relative;
  margin-left: auto;
}

.spin2win .gameFooter .timerWrapper .timerDuration {
  width: 200px;
  height: 40px;
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.spin2win .gameFooter .timerWrapper label {
  position: relative;
  z-index: 2;
  font-size: 60px;
  color: #fff;
  width: 100%;
  text-align: center;
}

.spin2win .gameFooter .timerWrapper label.currentTime {
  font-size: 28px;
  color: #000;
  bottom: -60px;
}

.spin2win .gameFooter .oppBtnWrapper {
  text-align: center;
}

.spin2win .gameFooter .oppBtnWrapper .oppBtn {
  width: 150px;
  height: 80px;
  margin-bottom: 10px;
  margin-right: 10px;
  position: relative;
  border: none;
  background-color: transparent;
  box-shadow: var(--base-shadow1);
}

.spin2win .gameFooter .oppBtnWrapper .oppBtn.disabled {
  opacity: 0.5;
  cursor: disabled;
}

.spin2win .gameFooter .oppBtnWrapper .oppBtn label {
  font-size: 36px;
  color: #fff;
  width: 100%;
  position: relative;
  z-index: 2;
  text-transform: uppercase;
}