/* From Uiverse.io by mnikita7767 */
.customCheckbox.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: max-content;
}

.customCheckbox.container label {
    cursor: pointer;
    position: relative;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 10px;
}

.customCheckbox label input {
    appearance: none;
}

.customCheckbox label span {
    width: 100%;
    height: 100%;
    position: absolute;
    background: linear-gradient(#fff, #ebf5fc, #ebf5fc);
    border-radius: 6px;
    box-shadow: inset 0 5px 1px 0 rgba(0, 0, 0, 0.1),
        0 5px 15px rgba(0, 0, 0, 0.1), 0 -5px 15px rgba(0, 0, 0, 0.15);
}

.customCheckbox.container label span::before {
    content: "";
    position: absolute;
    border-top: 1px solid #fff;
    inset: 5px 3px;
    filter: blur(5px);
}

.customCheckbox.container label input:checked~span {
    box-shadow: inset 0 -5px 0 0 rgba(0, 0, 0, 0.2),
        0 2px 10px rgba(0, 0, 0, 0.75);
    background: linear-gradient(#2b6b73, #31727a, #2b6b73);
}

.customCheckbox label p {
    position: relative;
    z-index: 10;
    font-size: 20px;
    color: #6d8e90;
    margin-bottom: 0;
}

.customCheckbox.container label input:checked~p {
    color: #fff;
    filter: drop-shadow(0 0 5px #00ffe2) drop-shadow(0 0 8px #00ffe2) drop-shadow(0 0 12px #00ffe2) drop-shadow(0 0 16px #00ffe2);
}