.joker12Card.gameCanvas {
  background-image: url("../../images/joker12card/bg.png");
  background-position: center;
  background-size: cover;
}

.joker12Card .cardHolder {
  width: 100%;
  /* height: 400px; */
  position: relative;
  padding: 10px 10px;
  background-image: url("../../images/joker12card/cardHolderBG.png");
  background-position: center;
  background-size: cover;
}

.joker12Card .cardHolder img.cardHolderBg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.joker12Card .cardHolder .cardItem {
  width: 20%;
  margin: 0 12px;
  margin-bottom: 10px;
  position: relative;
  z-index: 1;
  height: 120px;
}

.joker12Card .cardHolder .cardItem button {
  background: transparent;
  border: none;
  width: 100%;
  height: 100%;
  position: relative;
}

/* .joker12Card .cardHolder .cardItem button.cardButton {
    height: 175px;
} */

.joker12Card .cardHolder .cardItem button img.gameCard {
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.joker12Card .cardHolder .cardItem button img.cardRibbon {
  width: 140%;
  height: 55%;
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
}

.joker12Card .cardHolder .cardItem button.cardButton .playBtn {
  background-image: url("../../images/joker12card/whiteSquare.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  object-fit: contain;
  padding: 20px;
}

.joker12Card .cardHolder .cardItem.groupCard {
  margin: 0;
  width: 24%;
  height: 100px;
  margin-bottom: 10px;
}

.joker12Card .cardHolder .cardItem.groupCard button {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.joker12Card .cardHolder .cardItem.groupCard .houseCard {
  height: 70%;
  width: 55%;
  z-index: 1;
  margin-top: auto;
}

.joker12Card .cardHolder .cardItem.groupCard .houseCard img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.joker12Card .cardHolder .cardItem.groupCard::after {
  content: attr(data-title);
  position: absolute;
  top: 2px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  font-size: 16px;
  color: #000;
  width: 80%;
  text-align: center;
}

.joker12Card .cardHolder .cardItem.groupCard .groupCardBg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.joker12Card .cardHolder .cardItem.groupCard .playBtn {
  width: 45%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  position: relative;
  z-index: 1;
  margin-top: 30px;
}

.joker12Card .cardHolder .cardItem.groupCard .playBtn img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.joker12Card .cardHolder .cardItem.groupCard .playBtn::after {
  content: "PLAY";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  font-size: 14px;
  color: #000;
}

.joker12Card .cardHolder .cardItem.groupCard2 label {
  position: absolute;
  top: 58%;
  left: 60%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 20px;
}

.joker12Card .cardHolder .cardItem.infoBox {
  height: 50px;
}

.joker12Card .cardHolder .cardItem.infoBox .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  padding: 0 15px;
}

.joker12Card .cardHolder .cardItem.infoBox img {
  width: 100%;
  height: 100%;
}

/* .------------------------------- */
.joker12Card .betPortion {
  width: 65%;
}

.joker12Card .restPortion {
  flex: 1 0 0%;
}

/* ---------------------------- */

.joker12Card .chipWrapper {
  margin-right: 10px;
}

.joker12Card .chipBtn {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: none;
  background-color: transparent;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: 0.1s ease-in-out;
  z-index: 2;
}

.joker12Card .chipBtn img {
  z-index: 2;
}

.joker12Card .chipBtn.selected {
  transform: scale(1.1);
}

.joker12Card .chipBtn.selected label {
  font-family: Bungee Tint;
}

.joker12Card .chipBtn.selected img {
  -webkit-animation: chipRotate 4s infinite linear;
}

.joker12Card .chipBtn.selected::after {
  content: "";
  width: 70px;
  height: 70px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* background-color: yellowgreen; */
  border-radius: 50%;
  z-index: 1;
  -webkit-box-shadow: 0px 0px 45px 15px rgba(45, 255, 196, 0.9);
  -moz-box-shadow: 0px 0px 45px 15px rgba(45, 255, 196, 0.9);
  box-shadow: 0px 0px 45px 15px rgba(45, 255, 196, 0.9);
}

.joker12Card .chipBtn label.amount {
  position: relative;
  z-index: 3;
  color: #000;
  font-size: 22px;
}

/* -------------------------------- */
.joker12Card .timerWrapper {
  width: 120px;
  height: 120px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}

.joker12Card .timerWrapper label {
  color: #09ff00;
  position: relative;
  z-index: 4;
  font-size: 50px;
  font-family: "DS-Digital", sans-serif;
}

.joker12Card .timerWrapper img.timerBg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.joker12Card .timerWrapper img.timerSubBg {
  width: 90%;
  height: 90%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.joker12Card .timerWrapper .timerProgressWrapper {
  width: 90%;
  height: 90%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  --progress-percent: 0%;
}

.joker12Card .timerWrapper .timerProgressWrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

.joker12Card .timerWrapper .timerProgressWrapper::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: conic-gradient(#ffffff 0% var(--progress-percent), rgba(255, 255, 255, 0) 0%);
  mask: radial-gradient(circle, rgba(0, 0, 0, 1) 90%, rgba(0, 0, 0, 0) 100%);
  -webkit-mask: radial-gradient(circle, rgba(0, 0, 0, 1) 90%, rgba(0, 0, 0, 0) 100%);
  border-radius: 50%;
  z-index: 3;
  transition: background 0.3s linear;
  transform: scaleX(-1);
}

.joker12Card .timerWrapper img.timerDial {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}

/* -------------------------- */
.resultPanelWrapper {
  /* height: 200px; */
  width: 100%;
  background-image: url("../../images/joker12card/result-bg.png");
  background-position: center;
  background-size: cover;
  padding: 5px;
}

.resultPanelWrapper .resultPane.Last {
  width: 100%;
  height: 75px;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  background: radial-gradient(circle,
      rgba(255, 0, 0, 1) 0%,
      rgba(0, 0, 0, 1) 100%);
}

.resultPanelWrapper .resultPane.Last label {
  color: #fff;
}

.resultPanelWrapper .resultPane.Last .resultImages {
  width: auto;
  flex-direction: row;
  flex-wrap: wrap;
}

.resultPanelWrapper .resultPane.Last .resultImages img {
  width: 45px;
  height: 45px;
}

.resultPanelWrapper .resultPane {
  width: 100%;
  height: 75px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10px 7px;
}

.resultPanelWrapper .col-auto:nth-child(odd) .resultPane {
  background: linear-gradient(0deg,
      rgba(255, 209, 0, 1) 0%,
      rgba(255, 106, 0, 1) 100%);
}

.resultPanelWrapper .col-auto:nth-child(even) .resultPane {
  background: linear-gradient(0deg,
      rgba(255, 0, 0, 1) 0%,
      rgba(111, 0, 0, 1) 100%);
}

.resultPanelWrapper .resultPane label {
  color: #fff;
  position: relative;
  z-index: 1;
}

.resultPanelWrapper .resultPane .resultImages {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  z-index: 1;
  width: 100%;
  height: auto;
}

.resultPanelWrapper .resultPane .resultImages img {
  width: 20px;
  height: 100%;
  object-fit: contain;
}

.resultPanelWrapper .resultPane .gameTime {
  color: green;
  position: relative;
  z-index: 1;
}

