/* -------------------------------------- */
.joker12Card .wheelWrapper {
  width: 100%;
  height: 450px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -15px;
}

.joker12Card .wheelWrapper img.wheelOuter {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.joker12Card .wheelWrapper img.resultWheel {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 75%;
  margin-left: calc(-75% / 2);
  margin-top: calc(-75% / 2);
  z-index: 1;
}

.joker12Card .wheelWrapper img.resultWheel.inner {
  width: 50%;
  margin-left: calc(-50% / 2);
  margin-top: calc(-50% / 2);
}

.joker12Card .wheelWrapper .wheelMultiplier {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;

  text-align: center;
}

.joker12Card .wheelWrapper .wheelMultiplier label {
  font-size: 120px;
  font-family: "Bungee Inline";
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 50px;

  background: #cf0000;
  background: linear-gradient(to top, #cf0000 0%, #ffa500 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 2px rgb(255, 255, 255);
  text-stroke: 2px rgb(255, 255, 255);
  text-shadow: rgba(62, 66, 66, 0.4) 0px 10px 24px;
}

.joker12Card .wheelWrapper .marker {
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
  width: 95px;
}

.joker12Card .wheelWrapper .currentResult {
  position: absolute;
  top: 48%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 80px;
  z-index: 4;
  display: flex;
  flex-wrap: wrap;
}

.joker12Card .wheelWrapper .currentResult img {
  width: 50%;
}

.joker12Card .wheelWrapper .currentResult .multiplier {
  width: 100%;
  text-align: center;
}

.joker12Card .wheelWrapper .currentResult .multiplier label {
  font-size: 50px;
  font-family: "Bungee Inline";
  display: flex;
  justify-content: center;
  align-items: center;

  background: #cf0000;
  background: linear-gradient(to top, #cf0000 0%, #ffa500 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 0.5px rgb(255, 255, 255);
  text-stroke: 0.5px rgb(255, 255, 255);
  text-shadow: rgba(62, 66, 66, 0.4) 0px 10px 24px;
  height: 50px;
}