.header {
    background: #000;
    padding: 10px 0 0;
}

.header .tabGroup button {
    background: url('../../images//header/black_tab.png');
    border-radius: 10px 10px 0 0;
    overflow: hidden;
    border: none;
    background-size: cover;
    background-position: center;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    padding: 10px 40px;
    margin-right: 10px;
    font-family: Roboto Condensed;
}

.header .tabGroup button.active {
    background: url('../../images//header/lobby_tab.png');
}

.header .utilGroup {
    display: flex;
    align-items: center;
}

.header .utilGroup label {
    font-size: 30px;
    color: #ff7b00;
    font-family: Roboto Condensed;
    font-weight: 700;

}

.header .utilGroup input[type="checkbox"] {
    width: 30px;
    height: 30px;
    margin-right: 5px;
}

.header .utilGroup button.claimBtn {
    background-image: url('../../images/header/balance_border.png');
    background-position: center;
    background-size: contain;
    color: #fff;
    font-family: Roboto Condensed;
    padding: 0px 13px;
    border: none;
    border-radius: 5px;
    height: 30px;
}

.header .utilGroup input[type="text"] {
    height: 30px;
    margin-right: 5px;
}

.header .balanceGroup {
    display: flex;
    justify-content: space-between;
    position: relative;
    width: 180px;
    height: 35px;
}

.header .balanceGroup label {
    color: #fff;
    position: relative;
    z-index: 1;
    place-content: center;
    padding-left: 5px;
    font-family: Roboto Condensed;
    font-weight: 900;
}

.header .balanceGroup .balanceBox {
    background-image: url('../../images/header/balance_textbox.png');
    background-size: cover;
    background-position: center;
    position: relative;
    z-index: 1;
    width: 85px;
    left: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.header .balanceGroup img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: fill;
}

.header .userId {
    color: #ff7b00;
    font-size: 22px;
    margin-right: 10px;
    font-family: Roboto Condensed;
    font-weight: 900;
}

.header .exitButton {
    width: 30px;
    height: 30px;
    background-color: transparent;
    border: none;
    padding: 0;
}

.header .exitButton img {
    width: 100%;
    height: 100%;
    object-fit: fill;
}