.lobbyWrapper {
    background-image: url('../../images/lobby/lobbyWrapper.png');
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100vh;
}

.lobbyWrapper .slick-slider {
    top: 50px;
}

.lobbyWrapper .slick-slider .wrapper .imgWrapper {
    width: 700px;
    height: 500px;
    margin: auto;
}

.lobbyWrapper .slick-slider .wrapper .imgWrapper img {
    width: 100%;
    height: 100%;
}

.lobbyWrapper .slick-slider .wrapper h2 {
    text-align: center;
    margin-top: 85px;
    font-weight: 900;
    text-transform: uppercase;
    color: #ff0000;

    -webkit-text-stroke: 1px rgb(0, 0, 0);
    text-stroke: 1px rgb(0, 0, 0);
}

.lobbyWrapper .slick-slider .slick-prev {
    left: 130px;
}

.lobbyWrapper .slick-slider .slick-next {
    right: 130px;
}

.lobbyWrapper .slick-slider .slick-prev,
.lobbyWrapper .slick-slider .slick-next {
    top: 96%;
    width: 50px;
    height: 50px;
    z-index: 9999;
    opacity: 0;
}

.lobbyWrapper .slick-slider .slick-prev::before,
.lobbyWrapper .slick-slider .slick-next::before {
    font-size: 40px;
}