.loginWrapper {
    width: 100vw;
    height: 100vh;
    background-image: url('../../images/login/loginBG.png');
    background-position: center;
    background-size: cover;
}

.loginWrapper .formWrapper label,
.loginWrapper label {
    color: #fff;
}

.loginInput {
    /* border: 1px solid var(--btn-border);
    border-bottom: 1px solid #000; */
    border: none;
    color: #fff;
    padding: 5px;
    font-family: 'Lilita One';
    font-weight: 500;
    background-image: url('../../images/login/user_txt.png');
    background-size: cover;
    background-position: center;
}

.loginInput:focus {
    outline: none;
}

.loginButton {
    border: none;
    border-radius: 5px;
    color: #fff;
    font-family: 'Lilita One';
    font-weight: 500;
    font-size: 20px;
    background-image: url('../../images/login/button2.png');
    background-size: cover;
    background-position: center;
    width: 145px;
    height: 40px;
}

.loginHeading {
    font-size: 18px;
    font-weight: 500;
    padding-bottom: 20px;
    color: var(--color-yellow);
}

.loginMotherWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250px;
}

.loginWrapper .wrapper {
    background-image: url('../../images/login/rectangle.png');
    background-size: cover;
    background-position: center;
    height: 325px;
    display: flex;
    justify-content: center;
    align-items: end;
}

.loginRules {
    width: 100%;
    height: 500px;
}

.loginRules img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}